
import  Link  from 'next/link';
import { device } from '../breakpoints'

export default class Header extends React.Component{
    render(){
        return(
            <header>
                
                <Link href="/"><img className="logo" src="/static/logo.svg"/></Link>

                <nav className="Menu">

                    <Link href="/productos"><a>Productos</a></Link>
                    <Link href="/contacto"><a>Contacto</a></Link>
                    <Link href="/quienes-somos"><a>Quienes Somos</a></Link>
                    <Link href="/preguntas-frecuentes"><a>Preguntas Frecuentes</a></Link>
                    

                </nav>

                <style jsx>
                    {
                        `
                        
                        
                        header{
                            display:flex;
                            width:100%;
                            flex-direction:row;
                            align-items:center;
                            justify-content:space-around;
                            position:relative;
                            top:10px;
                        }
                        header img{
                            margin-left:20px;
                            max-width: 3%;
                            Cursor: Pointer;
                        }

                        header nav{
                            display:none;
                        }

                        header nav a{
                            text-decoration:none;
                            margin: 0 20px;
                            color:#113F67;
                        }

                        @media ${device.tablet}{
                            header nav{
                                display:flex;
                            }
                        }

                        
                        `
                    }
                </style>
            </header>
        )
    }
}